<template>
  <div class="demo">
    <Tips
      title="阴影"
      description="阴影来源于现实生活的反映物体与物体之间距离的物理现象。在界面中，我们往往通过模拟元素的投影直截了当的来告诉用户，元素之间的高度距离与层次关系"
    />
    <div class="item">
      <item-title
        name="阴影高度"
        desc="阴影是由两个不同阶层的平面产生，且强度由两者之间的距离决定。所以物体的高度直接影响物体的阴影，对象离地面越远阴影越大，模糊值越高。我们将系统分为无、低、中、高四个 UI 层级，各自分布在不同的高度层级，阴影属性也有所不同"
      />
      <img src="./assets/shadow.png" alt="" />
      <ul class="ul">
        <li>
          第0层（无阴影）：物体紧贴地面，投影与物体完全重叠，在界面中不对此层定义阴影值。如：输入框等
        </li>
        <li>
          第1层（小阴影）:
          物体位于低层级，此时物体被操作(悬停、点击等)触发为悬浮状态，当操作完成或取消时，悬停状态反馈也跟随消失，物体回归到原有的层级中，如：卡片
          hover 等
        </li>
        <li>
          第2层（中阴影）：物体位于中层级，此时物体与基准面的关系是展开并跟随，物体由地面上的元素展开产生，会跟随元素所在层级的移动而移动，如：下拉面板等
        </li>
        <li>
          第3层（大阴影）：物体位于高层级，该物体的运动和其他层级没有关联，如：对话框等
        </li>
      </ul>
    </div>
